
.marked {
  font-size: large;
  font-family: "Avenir Next";
}
.marked p > img {
    max-width: 100%;
}
.marked h1, .marked h2, .marked h3 {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.marked h1 {
    padding-bottom: 10px;
}
.marked h2 {
    padding-bottom: 5px;
}



